@font-face {
  font-family: "DM Sans local";
  src: url("../assets/DM_Sans/DMSans-VariableFont_opsz,wght.ttf") format("truetype-variations");
  font-weight: 100 900;
}
:root {
  --color-primary: #901c1c;
  --color-secondary: #db8876;
  --color-light: #fafafa;
  --color-dark: #312e2e;
  --color-neutral: #525252;
  --color-white: #ffffff;
  --color-black: #000000;
  --color-gray: #757575;
  --color-shadow: rgba(0, 0, 0, 0.1);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "DM Sans local", sans-serif;
}

#sticky-info-box {
  position: fixed;
  bottom: 0;
  right: 36px;
  padding: 21px 34px 16px 34px;
  background-color: var(--color-secondary);
  border-radius: 5px 5px 0 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 67.12px;
}

#sticky-info-box .sticky-info-box__likes-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 4.62px;
}

#sticky-info-box .sticky-info-box__total-likes {
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: 0;
  color: var(--color-black);
}

#sticky-info-box .sticky-info-box__price {
  font-weight: 500;
  font-size: 24px;
  line-height: 1;
  letter-spacing: 0;
}

/* Header */
.header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: start;
  height: 90px;
  padding-top: 42px;
}

.header__logo-link {
  margin-left: 100px;
}

.header__logo {
  height: 50px;
}

.header__logo-link:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 5px;
  border-radius: 5px;
}

/* Main */
#main {
  margin-top: 40px;
  padding: 0 100px;
  width: 100%;
}

/* Photograph Header */
#photographer-header {
  background-color: var(--color-light);
  height: 313px;
  padding: 0 100px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
  width: 100%;
}

.photographer-header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.photographer-header__left {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
}

.photographer-header__name {
  font-weight: 400;
  font-size: 64px;
  line-height: 83px;
  letter-spacing: 0;
  color: var(--color-secondary);
}

.photographer-header__location {
  font-weight: 400;
  font-size: 24px;
  line-height: 31px;
  letter-spacing: 0;
  display: inline;
  color: var(--color-primary);
}

.photographer-header__tagline {
  font-weight: 400;
  font-size: 18px;
  line-height: 23px;
  letter-spacing: 0;
  display: inline;
  color: var(--color-neutral);
}

.photographer-header__contact-button {
  color: var(--color-white);
  padding: 0 11px;
  width: 170px;
  height: 69px;
  border: none;
  background-color: var(--color-primary);
  border-radius: 5px;
  cursor: pointer;
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
  letter-spacing: 0;
  font-variation-settings: "wght" 700, "opsz" 27;
}

.photographer-header__contact-button:focus,
.photographer-header__contact-button:hover {
  color: var(--color-black);
  background-color: var(--color-secondary);
  border: none;
}

.photographer-header__right {
  width: 200px;
  height: 200px;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
  -webkit-mask-image: linear-gradient(to bottom, black 97%, transparent);
  mask-image: linear-gradient(to bottom, black 97%, transparent);
  box-shadow: 0px 0px 10px var(--color-shadow);
}

.photographer-header__right img {
  width: 526px;
  height: 358px;
  -o-object-fit: contain;
  object-fit: contain;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -42%);
}

/* Photograph Work */
.photographer-work {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: start;
  width: 100%;
  padding: 9px 0 65px 0;
}

.photographer-work__sorting {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 25px;
}

.photographer-work__sorting-label {
  font-weight: 700;
  font-size: 18px;
  line-height: 23px;
  letter-spacing: 0;
}

.photographer-work__sorting-select {
  width: 170px;
  border-radius: 5px;
  font-weight: 700;
  font-size: 18px;
  line-height: 69px;
  letter-spacing: 0;
}

.photographer-work__container {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 95px;
  row-gap: 20px;
  margin-top: 40px;
}

.photographer-work__media-card {
  width: 350px;
  height: 351px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 0;
}

.photographer-work__media-card .media {
  width: 350px;
  height: 300px;
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 5px;
}

.photographer-work__media-info {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 0;
}

.photographer-work__media-title {
  font-weight: 400;
  font-size: 24px;
  line-height: 31px;
  letter-spacing: 0;
  color: var(--color-primary);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.photographer-work__likes-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 4.62px;
}

.photographer-work__likes {
  font-weight: 500;
  font-size: 24px;
  line-height: 31px;
  letter-spacing: 0;
  display: flex;
  align-items: center;
  color: var(--color-primary);
}

.photographer-work__heart-icon {
  color: var(--color-primary);
  width: 19px;
  height: 19px;
}

.photographer-work__heart-icon.liked {
  fill: var(--color-primary);
}

/* Contact Modal */
.contact-modal {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.contact-modal__content {
  border-radius: 5px;
  background-color: var(--color-secondary);
  width: 669px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 35px;
  margin: auto;
  gap: 19px;
}

.contact-modal__header {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.contact-modal__close-button {
  cursor: pointer;
  width: 42px;
  height: 42px;
  margin-left: 15px;
  background-color: transparent;
  border: none;
}

.contact-modal__close-button:focus-visible {
  outline-offset: 4px;
  outline: 2px solid var(--color-black);
  border-radius: 5px;
}

.contact-modal__close-button img {
  width: 100%;
  height: 100%;
}

.contact-modal__photographer-name {
  font-weight: 400;
  font-size: 64px;
  line-height: 64px;
  letter-spacing: 0;
}

.contact-modal__form {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
}

.contact-modal__form-group {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: self-start;
}

.contact-modal__label {
  color: var(--color-dark);
  font-size: 36px;
}

.contact-modal__input {
  width: 100%;
  height: 68px;
  border: none;
  border-radius: 5px;
}

.contact-modal__textarea {
  width: 100%;
  height: 170px;
  border: none;
  border-radius: 5px;
}

.contact-modal__input,
.contact-modal__textarea {
  font-family: "DM Sans local", sans-serif;
  font-size: 22px;
  color: var(--color-dark);
  padding: 10px;
  font-weight: 400;
  line-height: 25px;
  letter-spacing: 0;
}

.contact-modal__submit-button {
  width: 170px;
  border: none;
  border-radius: 5px;
  background-color: var(--color-primary);
  color: var(--color-white);
  font-family: "DM Sans local", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 69px;
  letter-spacing: 0%;
  text-align: center;
  margin-top: 26px;
}

.contact-modal__submit-button:focus,
.contact-modal__submit-button:hover {
  background-color: var(--color-secondary);
  color: var(--color-black);
}

/* Lightbox */
.lightbox {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
  z-index: 1000;
  justify-content: center;
  align-items: center;
}

.lightbox[aria-hidden=false] {
  display: flex;
}

.lightbox__content {
  max-width: 80%;
  max-height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.lightbox__content div {
  background-color: var(--color-white);
  display: flex;
  flex-flow: column nowrap;
  padding-bottom: 12px;
  border-radius: 5px;
}

.lightbox__media {
  max-width: 1050px;
  max-height: 900px;
  border-radius: 5px;
  -o-object-fit: cover;
  object-fit: cover;
}

.lightbox__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 31px;
  letter-spacing: 0%;
  color: var(--color-primary);
  margin-top: 12px;
}

.lightbox__close,
.lightbox__prev,
.lightbox__next {
  position: absolute;
  background: none;
  border: none;
  font-size: 36px;
  cursor: pointer;
  z-index: 1001;
  display: flex;
}

.lightbox__close {
  top: 20px;
  right: 20px;
}

.lightbox__prev {
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
}

.lightbox__next {
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.lightbox__close:focus,
.lightbox__prev:focus,
.lightbox__next:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
  border-radius: 5px;
}

.lightbox__close path,
.lightbox__prev path,
.lightbox__next path {
  fill: var(--color-primary);
  stroke: var(--color-primary);
}

/* Custom Sort */
#custom-sort {
  position: relative;
  display: inline-block;
  width: 170px;
}

#custom-sort:focus-within {
  outline: 2px solid var(--color-primary);
  outline-offset: 4px;
  border-radius: 5px;
}

.custom-sort__button {
  width: 170px;
  height: 69px;
  display: flex;
  justify-content: space-between;
  flex-flow: row nowrap;
  align-items: center;
  padding: 0 20px;
  position: relative;
  z-index: 1;
  background-color: var(--color-primary);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.custom-sort__button[aria-expanded=true] {
  border-radius: 5px 5px 0 0;
}

.custom-sort__button[aria-expanded=true] .custom-sort__button-icon {
  transform: rotate(180deg);
}

.custom-sort__button-text {
  flex-grow: 1;
  text-align: left;
  width: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 23px;
  color: var(--color-white);
  border-radius: 5px;
  cursor: pointer;
  text-align: left;
}

.custom-sort__button-icon {
  margin-left: 10px;
  flex-shrink: 0;
  width: 16px;
  height: 11px;
  fill: var(--color-white);
}

.custom-sort__button:focus {
  outline: none;
}

.custom-sort__options {
  position: absolute;
  top: 66px;
  left: 0;
  width: 100%;
  background-color: var(--color-primary);
  display: none;
  z-index: 10;
  border-radius: 0 0 5px 5px;
  padding-bottom: 6px;
  box-shadow: 0px 10px 10px -10px var(--color-shadow);
}

.custom-sort__options[aria-hidden=false] {
  display: flex;
  flex-direction: column;
}

.custom-sort__option {
  padding: 14px 20px;
  text-align: left;
  width: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 23px;
  color: var(--color-white);
  border-radius: 5px;
  cursor: pointer;
  text-align: left;
}

.custom-sort__option::before {
  content: "";
  display: block;
  width: 150px;
  height: 1px;
  background-color: var(--color-white);
  transform: translate(-10px, -14px);
}

.custom-sort__option:hover,
.custom-sort__option:focus-visible {
  background-color: var(--color-secondary);
  color: var(--color-black);
  outline: none;
}

.custom-sort__option.selected {
  background-color: var(--color-primary);
  color: var(--color-white);
  font-weight: bold;
}